@use "base/mixins" as m;

.slick-arrow {
  visibility: hidden;
}

.brand {
  height: 20rem;
}

.brand-container {
  img {
    margin-left: auto;
    margin-right: auto;
    width: 8rem;
    height: 8rem;
    @include m.telefonoSM {
      width: 10rem;
      height: 10rem;
    }
    @include m.telefono {
      width: 12rem;
      height: 12rem;
    }
    @include m.desktop {
      width: 14rem;
      height: 14rem;
    }
  }
}

.brand-description {
  text-align: center;
  margin-top: 2rem;
  display: none;
  @include m.tablet {
    display: block;
  }
}

.carousel-brands {
  margin-top: 3.5rem;
  margin-bottom: 2rem;
}